.order {
  margin-top: 6.2rem;
}

.order__rectangles {
  margin-bottom: 2.7rem;
  display: grid;
  grid-template-rows: 1fr 1fr 1fr;
  grid-row-gap: 1.4rem;
}

.order__rectangle {
  background-color: $white;
  box-shadow: 0 3px 6px rgba(0, 0, 0, .16);
  display: flex;
  justify-content: space-between;
  padding: 0 4rem 0 1rem;
}

.order__rectangle-col {
  display: flex;
  align-items: center;
}

.order__rectangle-img {
  margin-right: 5rem;
}

.order__rectangle-title {
  font-weight: 700;
  color: $black;
}

.order__rectangle-price {
  margin-right: 12rem;
  font-size: 1.6rem;
  font-weight: 700;
  color: $blue;
}

.order__rectangle-amount {
  margin-right: 15rem;
  font-size: 1.6rem;
  font-weight: 700;
}

.order__rectangle-remove {
  width: 2.5rem;
  height: 2.2rem;
  background-color: $blue;
  padding: 0;
  position: relative;
  border: 0;
  border-radius: 4px;
  cursor: pointer;
  transition: transform .2s linear;
}

.order__rectangle-remove:after,
.order__rectangle-remove:before {
  content: "";
  display: block;
  width: 14px;
  height: 4px;
  background-color: $white;
  border-radius: 4px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -2px;
  margin-left: -7px;
}

.order__rectangle-remove:after {
  transform: rotate(-45deg);
}

.order__rectangle-remove:before {
  transform: rotate(45deg);
}

.order__squares {
  margin-bottom: 4.2rem;
  display: grid;
  grid-template-columns: 54rem 54rem;
  grid-column-gap: 3rem;
  grid-row-gap: 3rem;
}

.order__square {
  padding-top: 2.2rem;
  height: 457px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, .16);
  background-color: $white;
}

.order__square-inner {
  padding-right: 3rem;
  display: grid;
  grid-template-rows: 5rem 5rem 5rem 10rem;
  grid-row-gap: 2rem;

  &--last {
    grid-template-rows: repeat(4, 5rem);
  }
}

.order__square-title {
  padding-left: 3rem;
  margin-bottom: 3.5rem;
  font-family: 'Bebas Neue', sans-serif;
  font-size: 2.7rem;
  font-weight: 700;
  color: #23304d;
}

.order__square-input,
.order__square-textarea {
  margin-left: 2rem;
  font-family: 'Roboto',sans-serif;
  font-style: normal;
  font-size: 1.9rem;
  font-weight: 400;
  color: #6a6a6a;
  padding: 0 2rem;
  background-color: $white;
  border: 1px solid #707070;
  outline: 0;
  transition: border .2s linear;

  &:focus {
    border-color: $blue;
  }
}

.order__square-input {
  width: 32rem;
  height: 5rem;

  &--street {
    width: 19rem;
  }
}

.order__square-input.small {
  width: 5.5rem;
  padding: 0;
  margin: 0;
  text-align: center;
}

.order__square-input__small {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 1rem;
}

.order__square-textarea {
  padding-top: 16px;
  display: block;
  width: 32rem;
  height: 10rem;
  resize: none;
}

.order__square-label {
  display: flex;
  align-items: center;
  justify-content: flex-end;

  font-size: 1.9rem;
  color: #6a6a6a;
  cursor: pointer;
}

.order__square-label.br {
  text-align: right;
}

.order__choose-wrap {
  padding-left: 3rem;
  display: flex;
}

.order__square-choose {
  margin-right: 2rem;
  font-size: 1.9rem;
  color: #6a6a6a;
}

.order__radios {
  display: flex;
  flex-direction: column;

  input[type=radio] {
    display: none;
  }

  label {
    margin-bottom: 2.5rem;
    cursor: pointer;
    font-size: 1.7rem;
    user-select: none;

    &:last-of-type {
      margin-bottom: 0;
    }
  }

  label:before {
    content: '';
    display: inline-block;
    width: 18px;
    height: 18px;

    background-color: $white;
    border: 1px solid #707070;
    border-radius: 50%;

    flex-shrink: 0;
    flex-grow: 0;
    margin-bottom: -2px;
    margin-right: 14px;
    background-repeat: no-repeat;
    background-position: center center;
  }

  input[type=radio]:checked + label:before {
    background-image: url('../img/radio-checked.png');
  }
}

.order__square-many__inputs {
  display: grid;
  grid-template-columns: 1fr auto;
  grid-column-gap: 1rem;
}

.order__result {
  height: 11rem;
  background-color: #f8faff;
  box-shadow: 0 3px 6px rgba(0, 0, 0, .16);
  padding: 0 3rem;

  display: flex;
  justify-content: space-between;
  align-items: center;
}

.order__result-text {
  font-size: 1.9rem;

  span {
    margin-left: 2.5rem;
    color: #102a83;
    font-weight: 700;
  }
}

.order__result-btn {
  font-size: 2.1rem;
  padding: 2rem 6.4rem;
  border-radius: 31px;
}

@media (max-width: 1199px) {
  .order__squares {
    grid-template-columns: 54rem;
    justify-content: center;
  }

  .order__rectangle-price {
    margin-right: 5rem;
  }

  .order__rectangle-amount {
    margin-right: 5rem;
  }

  .order__square-label {
    font-size: 1.7rem;
  }

  .order__square-input {
    font-size: 1.6rem;
    height: 4rem;
  }

  .order__square-textarea {
    font-size: 1.6rem;
    height: 9rem;
  }

  .order__square-inner {
    grid-template-rows: 4rem 4rem 4rem 9rem;

    &--last {
      grid-template-rows: repeat(4, 4rem);
    }
  }

  .order__square {
    height: 42rem;
  }

  .order__result-btn {
    font-size: 1.8rem;
    padding: 1.5rem 4.5rem;
  }

  .order__result-text {
    font-size: 1.8rem;
  }
}

@media (max-width: 991px) {

  .order__rectangle {
    flex-direction: column;
    padding: 0 0 2rem 4rem;
  }

  .order__rectangle-price {
    margin-right: 10rem;
  }

  .order__rectangle-amount {
    margin-right: 10rem;
  }
}

@media (max-width: 767px) {
  .order__rectangle {
    padding: 0 2rem 2rem 2rem;
  }

  .order__rectangle-img {
    margin-right: 3rem;
  }

  .order__rectangle-price {
    margin: 0;
  }

  .order__rectangle-amount {
    margin: 0;
  }

  .order__rectangle-col:last-of-type {
    justify-content: space-between;
  }

  .order__squares {
    grid-template-columns: 1fr;
  }

  .order__square {
    height: auto;
    padding: 3rem 3rem 5rem 3rem;
  }

  .order__square-inner {
    padding: 0;
  }

  .order__square-title {
    padding: 0;
  }

  .order__choose-wrap {
    padding-left: 0;
  }

  .order__result-btn {
    font-size: 1.6rem;
    padding: 1.5rem 3rem;
  }
}

@media (max-width: 575px) {
  .order__rectangle-col:first-of-type {
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 3rem;
  }

  .order__square-input {
    margin: 0;
    width: 100%;
  }

  .order__square-textarea {
    margin: 0;
    width: 100%;
  }

  .order__square-label {
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 2.5rem;
    font-size: 1.6rem;
  }

  .order__square-input {
    margin-top: 1rem;
    font-size: 1.4rem;
    height: 3.5rem;
  }

  .order__square-textarea {
    margin-top: 1rem;
    height: 8rem;
  }

  .order__square-title {
    font-size: 2.5rem;
  }

  .order__square-inner {
    display: block;
    padding: 0;
  }

  .order__square-title {
    padding: 0;
  }

  .order__square-many__inputs {
    align-items: flex-end;
  }

  .order__square-label.br {
    text-align: left;
  }

  .order__square-many__inputs {
    display: block;
  }

  .order__square-input.small {
    width: 100%;
    padding: 0;
    margin: 0;
    text-align: center;
  }

  .order__choose-wrap {
    flex-direction: column;
  }

  .order__square-choose {
    margin: 0 0 3rem 0;
  }

  .order__radios label {
    font-size: 1.6rem;
  }

  .order__result {
    height: auto;
    padding: 3rem 1rem 3rem 3rem;
    flex-direction: column;
    align-items: flex-start;
  }

  .order__result-text {
    margin-bottom: 3rem;
  }

  .order__result-btn {
    padding: 1.5rem 4rem;
  }
}

@media (max-width: 349px) {
  .order__result-text span {
    display: block;
    margin-top: 1rem;
    margin-left: 0;
  }
}